import React from "react";
import { Helmet } from "react-helmet";
import { MapContainer, ScaleControl, Circle } from "react-leaflet";
import "leaflet/dist/leaflet.css";
import BaseMap from "../components/BaseMap";
import { NavLink } from "react-router-dom";
import L from 'leaflet';
import "leaflet-fullscreen/dist/Leaflet.fullscreen.js";
import "leaflet-fullscreen/dist/leaflet.fullscreen.css";
import { AllDamsOnly } from "../data/AllDamsOnly";
import ManualPDF from "../assets/Manual.pdf";
import { setInitialMapZoom, setDragging } from "../utilities/Functions";



const MyMarkers = () => {
  return AllDamsOnly.map((Dam, index) => (
    <Circle
      key={index}
      center={[Dam.geometry.coordinates[1], Dam.geometry.coordinates[0]]}
      fillColor="red"
      // radius={20000}
      color="red"
      opacity={1}
      interactive={false}
    ></Circle>
  ));
};

const maxBounds = L.latLngBounds(
  L.latLng(4, 60),  // Southwest corner of the bounds
  L.latLng(45, 110)  // Northeast corner of the bounds
);


const Dams = () => {
  return (
    <div>
      <Helmet>
        <title>Dams | GeoID</title>
      </Helmet>
      <div className="main-map">
        <div className="dam-list">
          <div className="heading">
            <h2>Indian Dams</h2>
            <p>
              <button className="BackButton">
                <NavLink to="/">&laquo; Back</NavLink>
              </button>
            </p>
          </div>

          <div className="select-heading">Select Dam List</div>
          <button className="classifyButton">
            <NavLink to="/dams/state-wise">
              <span>State Wise </span>
            </NavLink>
          </button>
          <button className="classifyButton">
            <NavLink to="/dams/basin-wise">
              <span>Basin Wise </span>
            </NavLink>
          </button>

          <button className="user-guide">
            <a href={ManualPDF} target="_blank" rel="noreferrer">
              <span>User Guide</span>
            </a>
          </button>
        </div>

        <div className="right-side">
          <MapContainer
            fullscreenControl={true}
            center={[23, 84]}
            style={{
              width: "100%",
              height: "100%",
            }}
            
            zoom={setInitialMapZoom()}
            maxZoom={8}
            zoomDelta={0.5}
            maxBounds={maxBounds}
            dragging={setDragging()}
            zoomSnap={0}
            minZoom={3}
            scrollWheelZoom={true}
          >
            <ScaleControl position="bottomleft" />

            <BaseMap />

            <MyMarkers />
          </MapContainer>
        </div>
      </div>
    </div>
  );
};

export default Dams;
